@import '/src/variable.scss';
.container {
    position: relative;
    width: fit-content;
}

.audioWrap {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
    pointer-events: none;
}
.videoWrap {
    position: relative;
    width: fit-content;
}
.counterWrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span:first-child {
        font-size: 20px;
        color: white;
    }
    span:last-child {
        font-size: 36px;
        font-weight: bold;
        color: white;
    }
}
.controlWrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 0 24px 25px;
    z-index: 10;
    transform: translate3d(0, 0, 0);
    .videoBackBtn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(calc(-100% - 50px), -50%);
        width: 90px;
        height: 50px;
        border: none;
        background-color: transparent;
        color: white;
    }
    .subtitleSet {
        position: absolute;
        bottom: 50px;
        left: 25px;
        width: 40px;
        height: 40px;
        border: none;
        background-color: white;
        cursor: pointer;
    }
    .mikeSet {
        position: absolute;
        bottom: 50px;
        left: 75px;
        width: 40px;
        height: 40px;
        border: none;
        background-color: white;
        cursor: pointer;
    }
    .playBtn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: none;
        background-color: initial;
        width: 76px;
        height: 76px;
        background-size: cover;
        &.play {
            background-image: url('./assets/btn_video_stop.png');
        }
        &.pause {
            background-image: url('./assets/btn_video_play.png');
        }
        img {
            width: 100%;
            height: 100%;
        }
    }
    .fullScreenBtn {
        width: 44px;
        height: 44px;
        background-color: transparent;
        border: none;
        margin: 0 0 9px auto;
        background-image: url('./assets/ic_full_screen.svg');
        background-size: cover;
        &.full {
            background-image: url('./assets/ic_minimum_screen.svg');
        }
    }
    .durationBar {
        position: relative;
        // display: flex;
        // align-items: center;
        input[type='range'] {
            width: 100%;
            -webkit-appearance: none;
            appearance: none;
            position: relative;
            z-index: 2;
            width: 100%;
            // &::-moz-range-thumb,
            &::-webkit-slider-runnable-track {
                -webkit-appearance: none;
                appearance: none;
                height: 4px;
            }
            &::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                border: none;
                width: 18px;
                height: 18px;
                background-color: white;
                border-radius: 50%;
                border: 4px solid $--primary;
                position: relative;
                margin: -7px 0;
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 5px;
                    height: 5px;
                }
            }
        }
        .timeline {
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            transform: translateY(-50%);
            -webkit-transform: translateY(-50%);
            background-color: $--disabled;
            height: 4px;
            .runningTime {
                width: 0px;
                height: 100%;
                background-color: $--primary;
            }
        }
    }
}
.progressBar {
    margin-top: 20px;
    width: 100%;
    height: 20px;
    display: flex;
    align-items: center;
    position: relative;
    max-width: 592px;
    margin: 0 auto;
    .progressArea {
        width: 100%;
        height: 20px;
        display: flex;
        align-items: center;
        .total {
            width: 100%;
            background-color: rgb(219, 219, 219);
            height: 4px;
            margin: 0 auto;
            .fill {
                height: 100%;
                background-color: $--primary;
            }
        }
    }
    .circle {
        position: absolute;
        // top: 50%;
        // transform: translateY(-50%);
        width: 16px;
        height: 16px;
        background-color: rgb(72, 82, 236);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after {
            display: block;
            content: '';
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: white;
        }
    }
}

:global(#root) {
    height: 300vh;
}
